import "../../styles/navtab.css";

import   React   from "react";
import { Link  } from "react-router-dom";

class NavTab extends React.Component
{
    id;
    name;
    icon;
    link;

    onHover = () =>
    {
        const name = document.getElementById(this.id);

        name.style.maxWidth     = name.scrollWidth + "px";
        name.style.borderTop    = "#B59959 solid";
        name.style.borderBottom = "#B59959 solid";
        name.style.borderRight  = "#B59959 solid";
        name.style.paddingRight = "15px";
    };

    onHoverExit = () =>
    {
        const name = document.getElementById(this.id);

        name.style.maxWidth     = null;
        name.style.borderTop    = "none";
        name.style.borderBottom = "none";
        name.style.borderRight  = "none";
        name.style.paddingRight = 0;
    };

    getTab = () =>
    {
        this.id = this.name + "Name";
        return (
            <Link className="NavTab" to={this.link} onMouseEnter={this.onHover} onMouseLeave={this.onHoverExit}>
                <img src={this.icon} alt={this.name}></img>
                <div id={this.id} className="Name">{this.name}</div>
            </Link>
        );
    };
}

export default NavTab;
